body {
  font-family: sans-serif;
}

html,
body,
#app,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Josefin Sans", sans-serif;
  overflow: hidden;
}

#app {
  background-color: black;
}

.center-text {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 50px;
}

.display-none {
  display: none;
}

.text-typing {
  white-space: nowrap;
  display: inline-block;
  animation: hello-typing 2s ease-in;
  animation-delay: 1s;
  width: 0;
  overflow: hidden;
  z-index: 2;
  position: relative;
  overflow: hidden;
}

.text-typing_4sec {
  animation: hello-typing-2 2s ease-in;
}

.zanaves {
  z-index: 1;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  background: url(https://ae01.alicdn.com/kf/HTB1Lpf9rYSYBuNjSspfq6AZCpXaA/SHANNY.jpg);
  background-size: 100% 100%;
  transition: opacity, transform 1s ease-in;
}

.zanaves_open {
  opacity: 1;
}

.zanaves_up {
  transform: translateY(-100%);
}

@keyframes zanaves-open {
  to {
    bottom: 100vh;
    top: -100vh;
  }
}

@keyframes hello-typing {
  from {
    width: 0;
    left: calc(100% / 2);
  }

  20%,
  to {
    width: 100%;
    left: 0;
  }

  to {
    opacity: 0;
  }
}

@keyframes hello-typing-2 {
  from {
    width: 0;
    left: calc(100% / 2);
  }

  40%,
  to {
    width: 100%;
    left: 0;
  }

  to {
    opacity: 0;
  }
}

#pinguin {
  display: none;
  width: 300px;
}

#cake {
  display: none;
  width: 400px;
}
